  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.boton-naranjo-block {
  background-color: #E08709;
  display: block;
  color: #FFFFFF;
  font-weight: 700;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2rem;
  border: none;
}
.boton-naranjo-block:hover {
  background-color: #f0ab4c;
  cursor: pointer;
}

.boton-naranjo {
  background-color: #E08709;
  display: inline-block;
  color: #FFFFFF;
  font-weight: 700;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2rem;
  border: none;
}
.boton-naranjo:hover {
  background-color: #f0ab4c;
  cursor: pointer;
}

.boton-verde {
  background-color: #71B100;
  display: inline-block;
  color: #FFFFFF;
  font-weight: 700;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2rem;
  border: none;
}
.boton-verde:hover {
  background-color: #b1e05f;
  cursor: pointer;
}

.boton-verde-block {
  background-color: #71B100;
  display: block;
  color: #FFFFFF;
  font-weight: 700;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2rem;
  border: none;
}
.boton-verde-block:hover {
  background-color: #b1e05f;
  cursor: pointer;
}

.boton-rojo-block {
  background-color: #b33b3b;
  display: block;
  color: #FFFFFF;
  font-weight: 700;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2rem;
  border: none;
  width: 100%;
}
.boton-rojo-block:hover {
  background-color: #e26060;
  cursor: pointer;
}

.dark-mode {
  background-color: #222222;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4, .dark-mode a {
  color: #f8f8f8;
}
.dark-mode .anuncio, .dark-mode .resumen-propiedad {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .anuncio .precio, .dark-mode .resumen-propiedad .precio {
  color: #71B100;
}
.dark-mode .anuncio .icono, .dark-mode .resumen-propiedad .icono {
  filter: invert(100);
}
.dark-mode .nosotros blockquote {
  color: #e1e1e1;
}
.dark-mode .resumen-entrada {
  background-color: #333333;
  padding: 1rem 2rem;
}
.dark-mode .formulario label,
.dark-mode .formulario legend,
.dark-mode .formulario p {
  color: #e1e1e1;
}
.dark-mode .formulario input:not([type=submit], [type=radio]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #333333;
  color: #e1e1e1;
  border: 1px solid #333333;
}
.dark-mode .formulario input:not([type=submit], [type=radio])::placeholder,
.dark-mode .formulario textarea::placeholder,
.dark-mode .formulario select::placeholder {
  color: #4d4c4c;
}
.dark-mode td {
  color: #f8f8f8;
}
.dark-mode .propiedades-tr:nth-child(odd) {
  background-color: #333333;
}
.dark-mode .propiedades-tr:nth-child(even) {
  background-color: #4d4c4c;
}

.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 65rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 65rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  max-width: 50rem;
  line-height: 6rem;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .boton-dark {
  filter: invert(100);
  width: 3rem;
  margin: 3rem 0;
}
.derecha .boton-dark:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha .boton-dark {
    margin-top: 0;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navegacion {
  height: 0rem;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.5s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    opacity: 1;
    visibility: visible;
    display: flex;
    align-items: flex-end;
    gap: 2rem;
  }
}
.navegacion a {
  color: #FFFFFF;
  font-size: 2rem;
  display: block;
  text-align: center;
}
.navegacion a:hover {
  color: #71B100;
}

@media (min-width: 768px) {
  .menu-movil {
    display: none;
  }
}
.menu-movil img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}
.icono p {
  text-align: center;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncio {
  background-color: #f8f8f8;
  border: 1px solid #e1e1e1;
  position: relative;
  padding-bottom: 5rem;
  margin-top: 2rem;
}
.anuncio .contenido-anuncio {
  padding: 1rem;
}
.anuncio h3, .anuncio p {
  margin: 0;
}
.anuncio p:first-of-type {
  text-align: center;
}
.anuncio .boton-naranjo-block {
  position: absolute;
  bottom: 0%;
  left: 0%;
  right: 0%;
}

.precio {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
  text-align: center;
}

.resumen-propiedad {
  padding: 0 2rem;
}

.iconos-prestaciones {
  display: flex;
  list-style: none;
  max-width: 40rem;
}
.iconos-prestaciones li {
  flex: 1;
  display: flex;
  gap: 0.5rem;
}
.iconos-prestaciones li img {
  height: 3rem;
  flex: 0 0 3rem;
}
@media (min-width: 768px) {
  .iconos-prestaciones li img {
    height: 2rem;
  }
}
@media (min-width: 1024px) {
  .iconos-prestaciones li img {
    height: 3rem;
  }
}
.iconos-prestaciones li p {
  font-weight: 900;
}
@media (min-width: 768px) {
  .iconos-prestaciones li p {
    font-size: 1.3rem;
  }
}
@media (min-width: 1024px) {
  .iconos-prestaciones li p {
    font-size: 2rem;
  }
}

.no-webp .imagen-contacto {
  background-image: url(../img/encuentra.jpg);
}

.webp .imagen-contacto {
  background-image: url(../img/encuentra.webp);
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 4rem;
  margin: 2rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #FFFFFF;
}
.imagen-contacto p {
  font-size: 2rem;
  color: #FFFFFF;
  text-align: center;
}

@media (min-width: 1024px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 4rem;
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
  }
}
@media (min-width: 768px) and (min-width: 1024px) {
  .entrada-blog {
    grid-template-columns: 1fr 2fr;
  }
}

.info-entrada span {
  color: #E08709;
}

.texto-entrada a {
  color: #000000;
}
.texto-entrada h4 {
  line-height: 1.5;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .texto-entrada h4 {
    margin: 0;
  }
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 20rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 0.5rem;
}
.texto-entrada p::first-letter {
  font-weight: 700;
}

.testimonio {
  background-color: #71B100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimonio blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonio blockquote::before {
  content: "";
  display: block;
  position: absolute;
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  left: -2rem;
}
.testimonio p {
  color: #FFFFFF;
  text-align: right;
  padding-right: 5rem;
}

.footer {
  padding: 2rem 0;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 2rem;
}
.footer .navegacion {
  height: auto;
  visibility: visible;
  opacity: 1;
}
@media (min-width: 768px) {
  .footer .navegacion {
    justify-content: center;
  }
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

@media (min-width: 768px) {
  .nosotros {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 3rem;
    align-items: center;
  }
}
.nosotros p:first-of-type::first-letter {
  font-weight: 700;
  font-size: 2.5rem;
}
.nosotros blockquote {
  font-size: 2.5rem;
  font-weight: 700;
  margin-left: 0;
}

.formulario p {
  font-size: 1.6rem;
  color: #333333;
  margin: 2rem 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario textarea {
  height: 20rem;
}
.formulario input:not([type=submit], [type=radio]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario input:not([type=submit], [type=radio])::placeholder,
.formulario textarea::placeholder,
.formulario select::placeholder {
  color: #e1e1e1;
}
.formulario .forma-contacto {
  display: flex;
  gap: 0.5rem;
}
.formulario input[type=radio] {
  margin-right: 2rem;
}
.formulario input[type=radio]:last-of-type {
  margin: 0;
}
.formulario input[type=submit], .formulario input[type=time], .formulario input[type=date] {
  width: min(100%, 35rem);
}
.formulario .forma-enviar {
  display: flex;
  justify-content: center;
  margin-bottom: 2rem;
}

.resumen-entrada {
  margin-bottom: 4rem;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  margin: 1rem 0;
}
.alerta.error {
  background-color: #b33b3b;
}
.alerta.exito {
  background-color: #71B100;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
  text-align: center;
}
table.propiedades thead {
  background-color: #71B100;
}
table.propiedades thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.propiedades .imagen-tabla {
  height: 15rem;
  width: auto;
}

.imagen-edit {
  height: 35rem;
  width: auto;
  margin-bottom: 3rem;
}